import React from 'react';
import { useState } from 'react';
import './projects.css';

const Projects = () => {
  const [activeTab, setActiveTab] = useState('web');

  return (
    <div className="section row" id="projects">
      <div className="tab-container">
        <button className={"tab " + (activeTab === 'web' ? 'active': '')} onClick={() => setActiveTab('web')}>WEB</button>
        <button className={"tab " + (activeTab === 'mobile' ? 'active': '')} onClick={() => setActiveTab('mobile')}>MOBILE</button>
      </div>
      {activeTab === 'web' ? (
        <div id="web" className="col s12">
          <Project
            img={require('../images/blog.png')}
            name="Fireblog"
            link="https://blog-harky.web.app/"
            description="A personal blog app with Firebase authentication and Firestore. Owners can create, edit and delete posts. Owners can set post title, excerpt and, with the help of Quill JS, post content."
          >
            <>
              <span className="react">React JS</span>
              <span className="js">Quill JS</span>
              <span className="js">Firebase</span>
              <span className="mcss">Materialize CSS</span>
            </>
          </Project>
          <Project
            img={require('../images/dassist.png')}
            name="Diyet Assist - PWA"
            link="https://diyet-assist.com"
            description="A practical calculation tool for Dietitians & Dietitian candidates. This is the Progressive Web App version of my Diyet Assist mobile app."
          >
            <>
              <span className="react">React JS</span>
              <span className="pwa">PWA</span>
            </>
          </Project>
          <Project
            img="https://raw.githubusercontent.com/iamharky/ReactJS-Chart-Generator/master/chart-generator.gif"
            name="Chart Generator"
            link="https://github.com/iamharky/ReactJS-Chart-Generator"
            description="A chart generator app built with ReactJS & ChartJS. Users can create charts with 6 different types, name them and download them."
          >
            <>
              <span className="react">React JS</span>
              <span className="js">Chart JS</span>
            </>
          </Project>
          <Project
            img="https://raw.githubusercontent.com/iamharky/Etch-A-Sketch/master/etch-a-sketch.gif"
            name="Etch A Sketch"
            link="https://iamharky.github.io/Etch-A-Sketch/"
            description="A simple Sketch app built with Vanilla JS."
          >
            <span className="js">JavaScript</span>
          </Project>
          <Project
            img="https://raw.githubusercontent.com/iamharky/kanban/master/kanban.gif"
            name="Kanban App"
            link="https://github.com/iamharky/kanban"
            description="A simple Kanban app with Drag-and-Drop that increases working efficiency by organizing tasks. Users can create new tasks, move the tasks between zones and delete them. The app stores tasks in localStorage for continuous usage."
          >
            <span className="js">JavaScript</span>
          </Project>
          <a className="see-more" target="_blank" rel="noopener noreferrer" href="https://github.com/iamharky">See my GitHub for more</a>
        </div>
        ):(
        <div id="mobile" className="col s12">
          <Project
            img={require('../images/dassist.png')}
            name="Diyet Assist"
            link="https://play.google.com/store/apps/details?id=dassist.harkytech.diyetassist"
            description="A practical calculation tool for Dietitians & Dietitian candidates. Built with React Native for mobile platforms. Available and has 2.5K+ downloads in Play Store."
          >
            <span className="react">React Native</span>
          </Project>
          <Project
            img="https://lh3.googleusercontent.com/zCzeOdqDIJe5C7gkPyG-fKlbTAdLz6LZplfpszffxpYcHRp1W5pnUluGfzDf6jvOBMg=s180-rw"
            name="Lîstika Peyvan"
            link="https://play.google.com/store/apps/details?id=tech.harky.peyv"
            description="Lîstika Peyvan is a word game that shuffles the letters of a word and asks for the correct order to find the word. It contains 35 levels and each level has 10 words to find.
              The game also has 5 difficulty levels and each difficulty level has 7 levels."
          >
            <>
              <span className="cordova">Cordova</span>
              <span className="vue">Vue JS</span>
            </>
          </Project>
          <Project
            img="https://lh3.googleusercontent.com/WgaWHxbCLvBHW3VsahZsW998KFQg9zR2TgR1MZ2CExJsLzGZDkukafNL6wroIx8L5n0=s180-rw"
            name="Mr. Robot Quotes"
            link="https://play.google.com/store/apps/details?id=com.mrrobotquotes"
            description="A simple React Native app that shows shareable quotes from legendary series 'Mr. Robot'."
          >
            <span className="react">React Native</span>
          </Project>
        </div>
      )}
    </div>
  );
};

const Project = ({ img, name, children, link, description }) => {
  return (
    <div className="project">
      <div className="project-image-area">
        <img src={img} alt={name} className="project-img" />
      </div>
      <div className="project-description-area">
        <span className="project-name">{name}</span>
        <p className="project-description">{description}</p>
        <hr />
        <p className="built-with">
          {children}
        </p>
        <div className="project-link-wrapper">
          <a className="project-link" target="_blank" rel="noopener noreferrer" href={link}>Go To Project</a>
        </div>
      </div>
    </div>
  );
};

export default Projects;