import React, { useState, useEffect } from 'react';
import './navbar.css';

const NavBar = () => {
  const [activeTab, setActiveTab] = useState('mabout');

  useEffect(() => {
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const handleScroll = () => {
    let top = document.getElementById('about').getBoundingClientRect().top;
    let height = document.getElementById('about').getBoundingClientRect().height;
    let projHeight = document.getElementById('projects').getBoundingClientRect().height;
    if (top <= 0 && top > -height / 2) {
      setActiveTab('mabout')
    } else if (top <= -height / 2 && top > -3 * height / 2) {
      setActiveTab('mskills')
    } else if (top <= -3 * height / 2 && top > (-3 * height / 2) - projHeight) {
      setActiveTab('mprojects')
    } else if (top <= (-3 * height / 2) - projHeight) {
      setActiveTab('mcontact')
    }
  }

  return (
    <div id="menu">
      <a id="mabout" className={activeTab === 'mabout' ? "active-tab" : null} href="#about">ABOUT</a>
      <a id="mskills" className={activeTab === 'mskills' ? "active-tab" : null} href="#skills">SKILLS</a>
      <a id="mprojects" className={activeTab === 'mprojects' ? "active-tab" : null} href="#projects">PROJECTS</a>
      <a id="mcontact" className={activeTab === 'mcontact' ? "active-tab" : null} href="#contact">CONTACT</a>
    </div>
  );
};

export default NavBar;