import React from 'react';
import './skills.css';

const Skills = () => {
  return (
    <div className="section z-depth-1" id="skills">
      <p className="skill row">
        <span id="dev">Development</span>
      </p>

      <p className="skill row">
        <span className="js">JavaScript :</span>
        <span className="react">React JS</span>
        <span className="react">React Native</span>
        <span className="svelte">Svelte</span>
        <span className="vue">Vue JS</span>
        <span className="pwa">PWA</span>
      </p>

      <p className="skill row">
        <span id="css">CSS3 :</span>
        <span className="scss">SASS</span>
        <span className="mcss">Materialize CSS</span>
      </p>

      <p className="skill row">
        <span id="html5">HTML5</span>
      </p>

      <p className="skill row">
        <span id="py">Python :</span>
        <span id="flask">Flask</span>
      </p>

      <p className="skill row">
        <span id="git">Git</span>
      </p>

      <p className="skill row">
        <span id="lang">Languages</span>
      </p>

      <p className="skill row">
        <span id="en">English</span>
        <span id="ku">Kurdish</span>
        <span id="tr">Turkish</span>
      </p>

    </div>
  );
};

export default Skills;